.container {
  padding: 24px;
  border: 1px solid #d8d8d8;
  border-radius: 8px;
  text-align: center;
  width: 100%;
}
.container p {
  font-size: 14px;
  line-height: 1.5em;
  font-weight: 300;
  color: #757575;
}
.container p:last-of-type {
  margin-bottom: 0;
}
.icon {
  margin-bottom: 8px;
  color: #201646;
  width: 28px;
  height: 28px;
}
.title {
  margin-bottom: 8px;
  margin-top: 0;
}
